:root {
  --flex-gap: 20px;
  --bg-color-card: white;
}

body {
  background-color: #a9a9a9;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

.photo-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  display: block;
}

.search-form {
  background-color: inherit;
  flex-direction: column;
  padding: 20px 0;
  display: flex;
}

.search-form input {
  max-width: 300px;
  text-align: center;
  border: 2px solid #3f0;
  border-radius: 10px;
  margin: 0 auto 30px;
  font-size: 30px;
}

.search-form button {
  max-width: 200px;
  background-color: #dcffc4;
  border: 3px solid #3f0;
  border-radius: 20px;
  margin: 0 auto;
  padding: 10px 30px;
  font-size: 20px;
}

.search-form button:hover {
  background-color: #0f7;
}

.search-form button:active {
  background-color: #ff0;
}

.gallery {
  max-width: 1400px;
  justify-content: center;
  gap: var(--flex-gap);
  flex-wrap: wrap;
  margin: 0 auto;
  display: flex;
  overflow-x: hidden;
}

.photo-card {
  width: calc((1440px - 3 * var(--flex-gap) ) / 5);
  background-color: var(--bg-color-card);
  color: #000;
  border-radius: 15px;
  font-size: 16px;
  font-style: italic;
  font-weight: 400;
  text-decoration: none;
  transition: transform .4s ease-out;
  overflow: hidden;
  box-shadow: 0 2px 1px rgba(46, 47, 66, .08), 0 2px 4px rgba(46, 47, 66, .16), 0 3px 6px rgba(46, 47, 66, .08);
}

.photo-card:hover {
  transform: scale(1.05);
}

.info {
  justify-content: space-around;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  margin: 0;
  padding: 10px 0;
  display: flex;
}

.info-item b {
  margin-bottom: 10px;
  font-style: normal;
  display: inline-block;
}

.is-hidden {
  visibility: hidden;
}

.load-more {
  background-color: black color: #dcffc4;
  border: 3px solid #3f0;
  border-radius: 20px;
  margin-top: 30px;
  margin-left: auto;
  margin-right: auto;
  padding: 10px 30px;
  font-size: 20px;
  font-style: italic;
  font-weight: 500;
  display: block;
}

.load-more:hover {
  background-color: #0ff;
}

.load-more:active {
  background-color: #ff0;
}

